@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.0/css/all.min.css');



.container{
  width: 90%;
  margin: auto;
  justify-content: center;
  text-align: center;
  background-color: bisque;
}

.input-fill{
  padding: 10px;
  outline: navy;
  border: none;
  border-radius: 15px;
  border-bottom: 2px solid #333;
  font-size: 20px;
  margin-top: 15px;
}

.list{
  list-style: none;
  color: aliceblue;
  font-size: 10px;
  display: flex;
  flex-wrap: wrap;

}

.list-item{
 /* visibility: hidden; */
  background-color: navy;
  padding: 10px;
  margin: 10px;
  width: 45%;
}

